<template lang="pug">
-
  let formList = [
    { label:'项目名称',name:'name'},
    { label:'施工许可证',name:'builderLicense',maxLength:18},
    { label:'项目所属区域',name:'projectPca',type:"jingHuaPca"},
    { label:'项目详情地址',name:'projectArea'},
    {
      label:'项目分类',name:'projectCategory',type:'select',
      options:[
        {label:"房屋建筑工程" ,":value":1},
        {label:"市政工程" ,":value":2},
        {label:"其他" ,":value":3},
      ]
    },
    { label:'项目上级政府',name:'governmentSector',type:'select', },
    {
      label:'项目状态',name:'projectStatus',type:'select',
      options:[
        {label:"在建" ,":value":1},
        {label:"竣工" ,":value":2},
        {label:"筹备" ,":value":3},
        {label:"立项" ,":value":4},
        {label:"停工" ,":value":5},
      ]
    },
    { label:'项目总投资',name:'investment'},
    { label:'工程开工时间',name:'projectStart',type:"time"},
    { label:'工程截止时间',name:'projectEnd',type:"time"},
    { label:'施工单位',name:'constructUnit'},
    { label:'统一社会信用代码',name:'creditCode',maxLength:18},
    { label:'法人代表',name:'legalPerson'},
    { label:'施工单位所属区域',name:'contructPca',type:'pca'},
    { label:'施工单位详细地址',name:'constructionArea'},
    { label:'联系人名称',name:'contactPerson'},
    { label:'联系人手机号',name:'contactPersonPhone',maxLength:11,phone:true},
  ]
.pd20
  .navigate-bar
    router-link(v-show="userType == 0" to="/front/prodDocking") 正式对接管理
    router-link(v-show="userType == 1" to="/examProject") 项目审核
    i.el-icon-arrow-right()
    span {{uuid?'查看项目详情':'创建项目申请'}}
  .big-form-wrap
    el-form( label-position="right" label-width="160px" :model="form" ref="form")
      el-row()
        each formItem in formList
          el-form-item.el-col.el-col-10( label=formItem.label prop=formItem.name verify phone=formItem.phone )
            if formItem.type == "time"
              el-date-picker(
                style="width:100%" placeholder=`请选择${formItem.label}`
                value-format="yyyy-MM-dd" type="date" :disabled="!!uuid"
                v-model=`form.${formItem.name}`)

            if formItem.type == 'jingHuaPca'
              el-cascader(
                style="width:100%;" :disabled="!!uuid"
                :options="require('@/utils/jinghua-pca.json')" v-model=`form.${formItem.name}`
                :clearable="!uuid")

            if formItem.type == 'pca'
              el-cascader(
                style="width:100%;" :disabled="!!uuid"
                :options="require('@/utils/pca.json')" v-model=`form.${formItem.name}`
                :clearable="!uuid")

            if formItem.type == 'select'
              el-select( style="width:100%;" v-model=`form.${formItem.name}` clearable :disabled="!!uuid")
                if !formItem.options
                  el-option(v-for="city in governmentList" :key="city.uuid" :label="city.itemValue" :value="city.uuid")
                else
                  each option in formItem.options
                    el-option&attributes(option)

            if !formItem.type
              el-input( placeholder=`请输入${formItem.label}` v-model=`form.${formItem.name}` :disabled="!!uuid")&attributes(formItem)
                if formItem.label == '项目总投资'
                  template(slot="append") 万元

        div.el-col.el-col-18.button-wrap
          el-button(v-show="userType == 0" @click="$router.push('/front/prodDocking')") 取消
          el-button(v-show="userType == 1" @click="$router.push('/examProject')") 取消
          el-button(v-show="!uuid" type="primary" @click="handleSubmit" :loading="confirmLoading") 提交
</template>
<script>
import utils from '@/utils/utils'
export default {
  data () {
    return {
      uuid: '',
      governmentList: [],
      form: {
        projectPca: []
      },
      confirmLoading: false,
    }
  },
  watch: {
    form: {
      deep: true,
      handler (newVal) {
        console.log(newVal)
        if (newVal.projectPca) {
          this.form.projectCounty = newVal.projectPca[2]
        }
        if (newVal.contructPca) {
          this.form.constructionProvince = newVal.contructPca[0]
          this.form.constructionCity = newVal.contructPca[1]
          this.form.constructionCounty = newVal.contructPca[2]
        }
      }
    }
  },
  computed: {
    userType () {
      return this.$store.state.userInfo.type
    }
  },
  beforeMount () {
    this.getGovernmentList()
    if (!this.$route.query.uuid) {
      return
    }
    this.uuid = this.$route.query.uuid
    this.getProject()
  },
  methods: {
    getGovernmentList () {
      (async () => {
        try {
          let res = await this.$ajax.post('/dict/government/get')
          this.governmentList = res.result
        } catch (error) {
          this.$message.error(error.msg || '获取项目上级政府列表失败')
        }
      })()
    },
    getProject () {
      (async () => {
        try {
          let { uuid } = this
          let res = await this.$ajax.post('/projectFront/getProjectByUuid', { uuid })
          let { projectCounty, constructionProvince, constructionCity, constructionCounty } = res.result
          // console.log(res.result)
          this.form = res.result
          this.form.projectPca = ['浙江省', '金华市', projectCounty]
          this.form.contructPca = [constructionProvince, constructionCity, constructionCounty]
        } catch (error) {
          this.$message.error(error.msg || '获取项目详情失败')
        }
      })()
    },
    handleSubmit () {
      (async () => {
        try {
          let valid = await this.$refs.form.validate()
          this.confirmLoading = true
          let res = await this.$ajax.post('/projectFront/add', this.form)
          this.$message.success('提交成功')
          this.confirmLoading = false
          this.$router.push('/front/prodDocking')
        } catch (error) {
          if (typeof error == 'boolean') {
            // this.$message.error('请先修复表单错误')
            return
          }
          this.confirmLoading = false
          this.$message.error(error.msg || '提交失败')
        }
      })()
    }
  }
}
</script>
<style lang="scss">
.form-wrap {
  width: 500px;
  .link {
    cursor: pointer;
  }
  .el-input.is-disabled .el-input__inner {
    // background: transparent;
    color: #333;
  }
}
.big-form-wrap {
  padding: 20px;
  background: #fff;
  border: 1px solid #d2d2d2;
  .button-wrap {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    .el-button {
      width: 200px;
    }
  }
  .el-input.is-disabled .el-input__inner {
    // background: transparent;
    color: #333;
  }
  .el-cascader.is-disabled .el-cascader__label {
    color: #333;
  }
}
</style>
